<template name='selection'>
		<view class="mySelector_wrapper">
			<image :src="images" mode="" class="icon"></image>
			<view class="Selector_title">
				{{mytitle}}
			</view>
		</view>
</template>

<script>
	export default {
		name:'selection',
		data() {
			return {
				images:'',
				mytitle:''
			};
		},
		props:{
			imageSrc:{
				type:String,
				value:''
			},
			title:{
				type:String,
				value:'选项卡'
			}
		},
		created() {
			let basic_src = "../../static/"
			let src = this.imageSrc
			this.images = basic_src + src;
			this.mytitle = this.title
		}
	}
</script>

<style>
	.mySelector_wrapper{
		width: 100%;
		height: 120upx;
		display: flex;
		flex-direction: row;
		align-items: center;
		padding-left: 40upx;
	}
	.icon{
		height: 70rpx;
		width: 70rpx;
	}
	.Selector_title{
		margin-left: 30upx;
		color: #777777;
	}

</style>
